import React from 'react';
import { Formik } from 'formik';
import ChainFormBody from './ChainFormBody';
import { _EDIT } from '../../../utils/Constants';
import { initialValues, ChainsSchema, updateChainsSchema } from './helper';

function ChainForm({ onHandleSubmit, chains, onClose, actionState, loading }) {
  return (
    <Formik
      initialValues={initialValues}
      onSubmit={(values, handles) => {
        console.log(values, 'TRTYUGHFJHNKJBJK<');
        onHandleSubmit(values, handles);
      }}
      validationSchema={
        actionState === true ? updateChainsSchema : ChainsSchema
      }>
      {props => {
        const {
          touched,
          values,
          errors,
          handleChange,
          handleSubmit,
          setFieldValue
        } = props;
        return (
          <ChainFormBody
            onClose={onClose}
            loading={loading}
            values={values}
            handleChange={handleChange}
            handleSubmit={handleSubmit}
            setFieldValue={setFieldValue}
            errors={errors}
            touched={touched}
            actionState={actionState}
            chains={chains}
          />
        );
      }}
    </Formik>
  );
}

export default ChainForm;
