import React, { useEffect } from 'react';
import Grid from '@mui/material/Grid';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Button from '@mui/material/Button';
import { TextField as MaterialTextField } from '@mui/material';
import Autocomplete from '@mui/material/Autocomplete';
import Paper from '@mui/material/Paper';

import TextField from '../../../shared/Inputs/TextField';
import { Chains as chainContractsArray } from '../../../../config/config';

import { CHAIN_NAME, _CHAIN_NAME, ABBREV, _ABBREV } from './constants';

const FilterByFormBody = ({
  loading,
  touched,
  values,
  errors,
  handleChange,
  handleSubmit,
  setFieldValue,
  resetForm,
  actionState,
  filter,
  setFilterObject,
  onResetFilterModal
}) => {
  useEffect(() => {
    if (filter) {
      setFieldValue(_CHAIN_NAME, filter.name ? filter.name : '');
      setFieldValue(_ABBREV, filter.abbrev ? filter.abbrev : '');
    }
  }, [filter, setFieldValue]);

  return (
    <form onSubmit={handleSubmit} autoComplete="off">
      <DialogContent dividers>
        <Grid container>
          <Grid item xs={12} sm={12} md={12} lg={12} className="p-2">
            <Autocomplete
              id={_CHAIN_NAME}
              options={chainContractsArray}
              getOptionLabel={option => option.name}
              PaperComponent={({ children }) => <Paper>{children}</Paper>}
              value={{
                name:
                  values.name !== ''
                    ? chainContractsArray.find(v => v.id === values.name).name
                    : ''
              }}
              onChange={(event, value) => {
                setFieldValue(_CHAIN_NAME, value.id);
              }}
              renderInput={params => (
                <MaterialTextField
                  {...params}
                  label={CHAIN_NAME}
                  name={_CHAIN_NAME}
                  className="custom-text-field"
                  size={'small'}
                  variant="outlined"
                  value={values.name}
                  helperText={touched.name && errors.name}
                  error={touched.name && Boolean(errors.name)}
                />
              )}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={12} className="p-2">
            <TextField
              label={ABBREV}
              value={values.abbrev}
              onChange={handleChange}
              name={_ABBREV}
              error={touched.abbrev && Boolean(errors.abbrev)}
              helperText={touched.abbrev && errors.abbrev}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className="modal-box-footer">
        <Button
          className="filter-reset-btn"
          onClick={() => {
            resetForm();
            onResetFilterModal();
          }}>
          Reset
        </Button>

        <Button type="submit" className="filter-filter-btn">
          Filter
        </Button>
      </DialogActions>
    </form>
  );
};

export default FilterByFormBody;
