export const setChainArr = data => {
  return data.map(d => {
    return {
      id: d.id ? d.id : '',
      name: d.name ? d.name.toLowerCase() : '',
      abbrev: d.abbrev ? d.abbrev.toLowerCase() : '',
      apiKey: d.apiKey ? d.apiKey : '',
      walletAddress: d.walletAddress ? d.walletAddress : '',
      contractAddress: d.contractAddress ? d.contractAddress : '',
      enabled: d.enabled || ''
    };
  });
};
export const filteredData = (searchedVal, data) => {
  const filteredRows = data.filter(row => {
    let match = false;
    // match  name
    if (!match && row.name) {
      match = row.name.includes(searchedVal.toLowerCase());
    }
    // match abbrev
    if (!match && row.abbrev) {
      match = row.abbrev.includes(searchedVal.toLowerCase());
    }
    // match apiKey
    if (!match && row.apiKey) {
      match = row.apiKey.includes(searchedVal.toLowerCase());
    }
    // match walletAddress
    if (!match && row.walletAddress) {
      match = row.walletAddress.includes(searchedVal.toLowerCase());
    }
    return match;
  });
  return filteredRows;
};
