import React, { useState, useCallback, useEffect } from 'react';
import ChainsList from './ChainsList';
import ChainsHeader from './ChainsHeader';

import {
  GetAdmins,
  CreateAdmin,
  UpdateAdmin,
  EnableOrDisableAdmin,
  ResentInvitation
} from '../../../services/admin/Admins';
import FormModal from '../../shared/Modals/FormModal';
import FilterForm from './Filter/FilterForm';

import ChainForm from './ChainForm';
import { success, error } from '../../shared/Helpers';
import {
  _SUCCESS,
  _ERROR,
  _ASC,
  _DESC,
  _ROLE,
  _NAME,
  _NEW,
  _CHAINS,
  ADMINS,
  _NO_CACHE,
  OFFSET
} from '../../../utils/Constants';
import { useQuery, useMutation } from '@apollo/client';
import {
  GET_ALL_CHAINS,
  CREATE_CHAIN,
  UPDATE_CHAIN
} from '../../../graphql/chain';
import { Button } from '@mui/material';
import FloatingButton from '../../shared/Button-float';
import { parseResponse } from '../../../utils';
import { suspendChain } from '../../../services/Chains';

function Chains() {
  const [isOpen, setIsOpen] = useState(false);
  const [chainLoading, setChainLoading] = useState(false);
  const [actionState, setActionState] = useState(false);
  const [chains, setChains] = useState(null);
  const [page, setPage] = useState(1);
  const [Data, setData] = useState([]);
  const [totalPages, setTotalPages] = useState(0);
  const offset = OFFSET;
  const [filterBy, setFilterBy] = useState('initial');
  const [filterObj, setFilterObj] = useState(null);
  const [fil, setFil] = useState({ page: page, offset: OFFSET });

  const { loading, error: cError, refetch, data } = useQuery(GET_ALL_CHAINS, {
    variables: fil
  });

  const [createChain] = useMutation(CREATE_CHAIN);
  const [updateChain] = useMutation(UPDATE_CHAIN);

  const closeModal = useCallback(() => {
    setActionState(false);
    setChains(null);
    setIsOpen(false);
  }, []);

  const openModal = useCallback(() => {
    setActionState(false);
    setChains(null);
    setIsOpen(true);
  }, []);

  useEffect(() => {
    (async () => {
      if (fil) {
        const res = await refetch(fil);
        const resp = await parseResponse(res, 'chains');
        setData(resp && resp);
      }
    })();
  }, [fil]);

  useEffect(() => {
    if (data) {
      (async () => {
        const resp = await parseResponse(data, 'chains');
        setData(resp && resp);
      })();
    }
  }, [data]);

  useEffect(() => {
    if (filterObj) {
      let obj = {};
      if (filterObj.name && filterObj.name !== '') {
        obj = {
          ...obj,
          name: filterObj.name
        };
      }
      if (filterObj.abbrev && filterObj.abbrev !== '') {
        obj = {
          ...obj,
          abbrev: filterObj.abbrev
        };
      }
      obj = { ...obj, page: page, offset: OFFSET };
      setFil(obj);
    }
  }, [filterObj]);

  const closeFilterModal = useCallback(() => {
    setFilterBy('initial');
    setIsOpen(false);
  }, []);

  const openFilterModal = useCallback(() => {
    setFilterBy('open');
    setIsOpen(true);
  }, []);

  const onResetFilterModal = useCallback(() => {
    setFilterObj({ page: page, offset: offset });
  }, []);

  const setFilterObject = useCallback(obj => {
    setFilterObj(obj);
  }, []);

  const onClickEdit = useCallback(obj => {
    setActionState(true);
    setChains(obj);
    setIsOpen(true);
  }, []);

  const onHandleEnableChange = async (enabled, id) => {
    const resp = await suspendChain({ id: id, enabled: enabled });
    if (resp.status === 200) {
      success(resp.message);
    } else {
      error(resp.message);
    }

    refetch(fil);
  };

  const refreshChainList = () => {
    refetch(fil);
  };

  const onHandleSubmit = async (values, handles) => {
    setChainLoading(true);
    if (!actionState) {
      const response = await createChain({
        variables: {
          // name: values.name,
          // abbrev: values.abbrev,
          apiKey: values.apiKey,
          walletAddress: values.walletAddress
          // contractAddress: values.contractAddress
        }
      });

      const resp = await parseResponse(response, 'createChain');

      if (resp.code === 200) {
        setChainLoading(false);
        success(resp.message);
        handles.resetForm();
        refreshChainList();
        closeModal();
      } else {
        setChainLoading(false);

        error(resp && resp.message);
      }
    } else {
      const response = await updateChain({
        variables: {
          updateChainId: values.id,
          // name: values.name,
          // abbrev: values.abbrev,
          apiKey: values.apiKey,
          walletAddress: values.walletAddress
        }
      });
      const resp = await parseResponse(response, 'updateChain');

      if (resp.code === 200) {
        setChainLoading(false);
        success(resp.message);
        handles.resetForm();
        closeModal();
      } else {
        setChainLoading(false);
        error(resp && resp.message);
      }
    }
    refreshChainList();
  };

  const onHandleFilter = async (values, handles) => {
    setFilterObject(values);
  };

  return (
    <div className="w-full h-full">
      <FloatingButton
        type={_CHAINS}
        currencyType={_CHAINS}
        refetch={refreshChainList}
        handleModalOpen={openModal}
      />
      <ChainsList
        data={Data}
        loading={loading}
        page={page}
        setPage={setPage}
        offset={offset}
        totalPages={totalPages}
        onClickEdit={onClickEdit}
        onHandleEnableChange={onHandleEnableChange}
        // onResentInvitation={onResentInvitation}
        // refreshAdminList={refreshAdminList}
        filterBy={filterBy}
        disable={isOpen}
        openFilterModal={openFilterModal}
      />

      {filterBy === 'initial' ? (
        <FormModal
          maxWidth="sm"
          open={isOpen}
          onClose={closeModal}
          title={!actionState ? 'Create Chain' : 'Edit Chain'}>
          <ChainForm
            onHandleSubmit={onHandleSubmit}
            loading={chainLoading}
            onClose={closeModal}
            actionState={actionState}
            chains={chains}
          />
        </FormModal>
      ) : (
        <FormModal
          maxWidth="sm"
          open={isOpen}
          onClose={closeFilterModal}
          title={'Filter Chains'}>
          <FilterForm
            onHandleSubmit={onHandleFilter}
            loading={loading}
            onClose={closeModal}
            actionState={actionState}
            filter={filterObj}
            setFilterObject={setFilterObject}
            onResetFilterModal={onResetFilterModal}
          />
        </FormModal>
      )}
    </div>
  );
}

export default Chains;
