import React from 'react';
import { Formik } from 'formik';
import FilterFormBody from './FilterByFormBody';
import { initialValues, UserSchema } from './helper';

const FilterForm = ({
  loading,
  onHandleSubmit,
  onClose,
  actionState,
  filter,
  setFilterObject,
  onResetFilterModal
}) => {
  return (
    <Formik
      initialValues={initialValues}
      onSubmit={(values, handles) => {
        onHandleSubmit(values, handles);
      }}
      validationSchema={UserSchema}>
      {props => {
        const {
          touched,
          values,
          errors,
          handleChange,
          handleSubmit,
          setFieldValue,
          resetForm
        } = props;
        return (
          <FilterFormBody
            onClose={onClose}
            loading={loading}
            values={values}
            handleChange={handleChange}
            handleSubmit={handleSubmit}
            setFieldValue={setFieldValue}
            resetForm={resetForm}
            errors={errors}
            touched={touched}
            actionState={actionState}
            filter={filter}
            setFilterObject={setFilterObject}
            onResetFilterModal={onResetFilterModal}
          />
        );
      }}
    </Formik>
  );
};

export default FilterForm;
