import React, { useState, useEffect, useCallback } from 'react';
import { EditIcon } from '../../../icons';
import { headCells, setAdminArr, filteredData } from './helper';
import { BiRevision } from 'react-icons/bi';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TablePagination from '@mui/material/TablePagination';
import IconButton from '@mui/material/IconButton';
import TableRow from '@mui/material/TableRow';
import Tooltip from '@mui/material/Tooltip';
import Paper from '@mui/material/Paper';
import TableHeader from '../Shared/TableHeader';
import { stableSort, getComparator } from '../Shared/Helper';
import { useHistory } from 'react-router-dom';
import Switch from '@mui/material/Switch';
import Toolbar from '../Shared/Toolbar';
import { MdContentCopy } from 'react-icons/md';
import { TableLoader, EmptyTableRow } from '../../shared/Tables';
import { _NAME, CHAINS, API_KEY_COPY, WALLET_COPY } from './constants';
import {
  ASC,
  DESC,
  ROWS_PER_PAGE,
  ROWS_PER_PAGE_OPTIONS,
  CENTER,
  EDIT,
  _ACTIVE,
  _INACTIVE,
  _TOP,
  SMALL
} from '../../shared/Constants';
import ComponentHeader from '../../shared/ComponentHeader';
import InputCheckbox from '../../shared/Checkboxes/Checkbox';
import { parseResponse, copyToClipboard } from '../../../utils';
import InputSwitch from '../../shared/switches/Switch';
import DialogBox from '../../shared/DialogBox';

function ChainsList({
  data,
  loading,
  onClickEdit,
  onHandleEnableChange,
  // refreshAdminList,
  // onResentInvitation
  filterBy,
  isOpen,
  openFilterModal
}) {
  const [chains, setChains] = useState([]);
  const [tempChains, setTempChains] = useState([]);
  const [order, setOrder] = useState(ASC);
  const [orderBy, setOrderBy] = useState(_NAME);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(ROWS_PER_PAGE);
  const [checked, setChecked] = useState(false);
  const [dialog, setDialog] = useState({ visible: false });

  const history = useHistory();

  useEffect(() => {
    if (data && data.data && data.data.length > 0) {
      setChains(data.data);
      setTempChains(data.data);
      setPage(data.current);
    } else {
      setChains([]);
      setTempChains([]);
      setPage(1);
    }
  }, [data]);

  const requestSearch = useCallback(
    searchedVal => {
      if (!searchedVal) {
        setChains(tempChains);
      }
      setChains(filteredData(searchedVal, tempChains));
    },
    [tempChains]
  );

  const handleRequestSort = useCallback(
    (event, property) => {
      const isAsc = orderBy === property && order === ASC;
      setOrder(isAsc ? DESC : ASC);
      setOrderBy(property);
    },
    [order, orderBy]
  );

  const handleChangePage = useCallback((event, newPage) => {
    setPage(newPage);
  }, []);

  const handleChangeRowsPerPage = useCallback(event => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  }, []);

  const handleChains = id => {
    history.push(`/chains/chainsAccount/${id}`);
  };

  const handleUpdateStatusConfirmation = (status, id) => {
    setDialog({
      id: id,
      visible: true,
      header: status ? 'Unsuspend Chain?' : 'Suspend Chain?',
      message: 'Are you sure you want to do this?',
      key: Math.random()
        .toString(36)
        .substring(6),
      ok: () => onHandleEnableChange(status, id)
    });
  };

  return (
    <>
      <DialogBox {...dialog} />

      <div className="">
        <ComponentHeader
          data={chains}
          setData={setTempChains}
          page={page}
          count={data && data.count}
          rowsPerPage={rowsPerPage}
          setPage={setPage}
          onSearch={requestSearch}
          type={'button'}
          filterBy={filterBy}
          disable={isOpen}
          openFilterModal={openFilterModal}
        />
      </div>

      <TableContainer className="table-cont overflow-auto">
        <Table sx={{ width: '100%' }} aria-labelledby={CHAINS}>
          <TableHeader
            order={order}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
            headCells={headCells}
          />

          <TableBody>
            {loading ? (
              <TableLoader headCells={headCells} />
            ) : chains && chains.length > 0 ? (
              stableSort(chains, getComparator(order, orderBy)).map(row => {
                return (
                  <TableRow key={row.id}>
                    <TableCell
                      align={CENTER}
                      style={{ textTransform: 'capitalize' }}
                      className="row-cell">
                      <InputCheckbox />
                    </TableCell>
                    <TableCell
                      align={CENTER}
                      style={{ textTransform: 'uppercase' }}
                      className="row-cell">
                      <div
                        style={{ cursor: 'pointer' }}
                        onClick={() => handleChains(row.id)}>
                        {row.name && row.name}
                      </div>
                    </TableCell>
                    <TableCell
                      align={CENTER}
                      style={{ textTransform: 'capitalize' }}
                      className="row-cell">
                      {row.abbrev && row.abbrev}
                    </TableCell>
                    {/* <TableCell align={CENTER} className="row-cell">
                      <Tooltip title={API_KEY_COPY}>
                        <div className="flex justify-end">
                          <MdContentCopy
                            className="cursor-pointer"
                            onClick={() => copyToClipboard(row.apiKey)}
                          />
                        </div>
                      </Tooltip>
                      <Tooltip title={row.apiKey}>
                        <div>
                          {row.apiKey && row.apiKey.substring(0, 20)}...
                        </div>
                      </Tooltip>
                    </TableCell> */}
                    <TableCell align={CENTER} className="row-cell">
                      {/* <Tooltip title={WALLET_COPY}>
                        <div className="flex justify-end">
                          <MdContentCopy
                            className="cursor-pointer"
                            onClick={() => copyToClipboard(row.walletAddress)}
                          />
                        </div>
                      </Tooltip> */}
                      <Tooltip title={row.walletAddress}>
                        <div>
                          {row.walletAddress &&
                            row.walletAddress.substring(0, 20)}
                          ...
                        </div>
                      </Tooltip>
                    </TableCell>
                    <TableCell align={CENTER} className="row-cell">
                      <Tooltip
                        title={
                          row.enabled ? 'Chain Enabled' : 'Chain Disabled'
                        }>
                        <div>
                          <InputSwitch
                            disabled={false}
                            checked={row.enabled}
                            handleChange={e =>
                              handleUpdateStatusConfirmation(
                                e.target.checked,
                                row.id
                              )
                            }
                          />
                        </div>
                      </Tooltip>
                    </TableCell>
                    <TableCell align={CENTER} className="row-cell">
                      <div>
                        <Tooltip title="Edit Chain">
                          <div>
                            {' '}
                            <IconButton
                              aria-label={EDIT}
                              size={SMALL}
                              onClick={() => onClickEdit(row)}
                              className="dark:text-blue-600">
                              <EditIcon className="h-5 w-5 text-blue-600 dark:text-blue-300" />
                            </IconButton>
                          </div>
                        </Tooltip>
                      </div>
                    </TableCell>
                  </TableRow>
                );
              })
            ) : (
              <EmptyTableRow headCells={headCells} />
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}

export default ChainsList;
