import instance from '../../config/axios';
import { ParseError, getAuthHeader } from '../../config/utils';
import { isObject } from 'lodash';
import axios from 'axios';

export const suspendChain = async (formData) => {
  const responseData = {
    loading: false,
    status: 210,
    message: 'Something went wrong, Please try again.'
  };

  const headers = await getAuthHeader();
  // const data = { id: id, enabled: status };
  return instance
    .post('/admin/chain/enabled', formData, headers)
    .then((response) => {
      if (response.data && response.data.code === 200) {
        response = response.data;
        return {
          ...responseData,
          data: isObject(response.data) ? response.data : {},
          status: 200,
          message: response.message
        };
      } else {
        return {
          ...responseData,
          message: ParseError(response.data)
        };
      }
    })
    .catch((err) => {
      // console.log(err, 'Erorr>>>>>>>>>>>>>>>>>>>>>>>>>>>..');
      return {
        ...responseData,
        message: ParseError(
          err.response && err.response.data ? err.response.data : err.message
        )
      };
    });
};
