import { gql } from '@apollo/client';

export const GET_ALL_CHAINS = gql`
  query Query($page: Int, $offset: Int, $name: String, $abbrev: String) {
    chains(page: $page, offset: $offset, name: $name, abbrev: $abbrev) {
      code
      message
      data {
        brokerId
        id
        name
        abbrev
        apiKey
        walletAddress
        contractAddress
        enabled
      }
      count
      total
      current
    }
  }
`;
// $name: String
// $abbrev: String

// name: $name
// abbrev: $abbrev

// $contractAddress: String

// contractAddress: $contractAddress

export const CREATE_CHAIN = gql`
  mutation Mutation($apiKey: String, $walletAddress: String) {
    createChain(apiKey: $apiKey, walletAddress: $walletAddress) {
      code
      message
      data {
        name
        abbrev
        apiKey
        walletAddress
        contractAddress
      }
    }
  }
`;

// $name: String
// $abbrev: String

// name: $name
// abbrev: $abbrev

export const UPDATE_CHAIN = gql`
  mutation Mutation(
    $updateChainId: ID
    $apiKey: String
    $walletAddress: String
  ) {
    updateChain(
      id: $updateChainId
      apiKey: $apiKey
      walletAddress: $walletAddress
    ) {
      code
      message
      data {
        id
        name
        abbrev
        apiKey
        walletAddress
      }
    }
  }
`;
export const GET_ALL_CHAINS_ACCOUNT = gql`
  query Query($page: Int, $offset: Int, $chainId: ID) {
    allChainAccountByChainId(page: $page, offset: $offset, chainId: $chainId) {
      count
      total
      current
      data {
        chainId {
          name
        }
        id
        apiKey
        walletAddress
        walletIndex
        enabled
        createdAt
        updatedAt
      }
    }
  }
`;
